<script>
    export default {
        name: "Login",
        data(){
            return {
                account:'',
                password:'',
                passwordSign:true,
                checked: false,
                isAccountActive:false,
                isPasswordActive:false,

            }
        },
        created(){

        },
        components:{

        },
        methods:{
            //账号底
            handleBoder(){
                console.log(1)
                this.isAccountActive = true;
            },
            handleBoder1(){
                console.log(1)
                this.isPasswordActive = true;
            },
            cancleBoder(){
                this.isAccountActive = false;
            },
            cancleBoder1(){
                this.isPasswordActive = false;
            },
            toLogin(){
                var that = this;
                if (this.account==''){
                    this.$message({
                        message: '请输入用户名',
                        type: 'warning'
                    });
                    return false
                }
                if (this.password==''){
                    this.$message({
                        message: '请输入密码',
                        type: 'warning'
                    });
                    return false
                }
                if (!this.checked){
                    this.$message({
                        message: '请勾选用户协议',
                        type: 'warning'
                    });
                    return false
                }
                this.$axios.post('/user/userLogin', this.$qs.stringify({
                    userName:this.account,
                    password:this.password
                })).then(function(res) {

                    if (res.data.code == 0){
                        that.$message({
                            message: '登陆成功',
                            type: 'success'
                        });
                        console.log(res);
                        sessionStorage.setItem('username',res.data.data.userName)
                        that.$cookies.set('username',res.data.data.userName)
                        sessionStorage.setItem('status',res.data.data.status)
                        sessionStorage.setItem('email',res.data.data.email)
                        console.log(that.$store.state.status)
                        that.$router.push('/')
                    }else {
                        that.$message.error(res.data.msg)

                    }
                })
                    .catch(err=>{
                        that.$message.error('网络连接错误，请稍后再试')
                    })


            },
            toRegister(){
                this.$router.push('/register');
            },
            //切换密码
            togglePassword(){
                var password = this.$refs.password;
                if (this.passwordSign){
                    password.type="text";
                    this.passwordSign=false;
                }else  {
                    password.type="password";
                    this.passwordSign=true;
                }

                console.log(password)
            }
        }
    }
</script>

